 </template>
 
 <script>
 import "@/assets/css/style.css";
 import  axios from 'axios'
 import barbrush from '@/views/graphs/barbrush.vue'
 export default {
    computed:{
        imgPath(name){
            return '@/assets/images/each/'+name +'.png'
        }
    },
   components:{barbrush},
   data(){
    
    return{
        borderColors:{
            'Creating an audiovisual experience':"#8da0cb", 'Making data tangible':"#e78ac3",
       'Telling interactive stories':"#a6d854", 'Polishing graphics':"#66c2a5",
       'Bringing abstract data into a concrete scene':"#fc8d62"
        },
        techniqueColors:{
            'color':"#5470c6", 'shape/pattern':"#5470c6", 'imagery':"#5470c6", 'sound/music':"#5470c6", 'animation':"#5470c6",
            'metaphor/analogy':"#91cc75", 'GUI interaction':"#fac858", 'material ':"#ee6666", 'style':"#5470c6", 'light':"#5470c6",
            'storyline/plot':"#91cc75", 'synchronous/responsive designeffect':"#fac858", 'layout':"#5470c6", 
            'large immersive screen':"#ee6666", 'montage/collage':"#91cc75", 'perspective of machine':"#91cc75",
            'co-design/user generated content':"#91cc75", 'AR/VR/MR':"#fac858", 'body interaction':"#fac858", 'facial interaction':"#fac858", 
            'typography':"#5470c6", 'situated environment':"#ee6666", 'personalization':"#91cc75", 
            'touch/manipulate':"#fac858", 'bionics-triggered interaction':"#fac858", 'gesture interaction':"#fac858", 'plants':"#ee6666", 'voice interaction':"#fac858", 'draw/sketch interaction':"#fac858", 
            'smell':"#5470c6", 'robot':"#ee6666", 'bacteria':"#ee6666",'temperature':"#5470c6", 'taste':"#5470c6"
        },
        canvas: null,
      ctx: null,
      particles: [],
      numParticles: 0,
        // t:"XEPA", 
        banner:require('@/assets/images/first/a.jpg'),        
        times:['1996', '2002', '2003', '2004', '2006', '2007', '2009', '2010', '2011', '2012', '2013', '2014', '2015', '2016', '2017', '2018', '2019', '2020', '2021', '2022', '2023'],
        // artPapers:['IEEE VISAP art show', 'IEEE VISAP paper', 'IEEE VISAP pictorial/annotated portfolio', 'Recommended', 'PacificVis', 'UbiComp', 'TVCG', 'CAe', 'HCII', 'Creativity & Cognition'],
        artPapers:['IEEE VIS Arts Program','other venues'],
        designCategories:['Creating an audiovisual experience', 'Making data tangible',
       'Telling interactive stories', 'Polishing graphics',
       'Bringing abstract data into a concrete scene'],
        designParadigms: ['generative art', 'data sonification', 'ambient display', 'physical computing', 'speculative design', 'digital humanism', 'algorithmic art', 'data physicalization', 'abstract art', 'computational aesthetic evaluation', 'neuroaesthetics', 'computer art', 'graphic art', 'abstract cinema/cinematography', 'experimental animation', 'digital photograph', 'electronic art', 'digital fabrication', 'spatial narrative', 'neo-concrete art', 'aministic design', 'glitch art', 'data humanization', 'parametric design', 'experimental media archeology', 'abstract animation', 'digital art', 'machine learning', 'digital audio signal processing', 'generative storytelling', 'generative artifact', 'data manifestation', 'wearable art', 'compter-generated art', 'bio-data art', 'VR art', 'medical data art', 'Participatory Action Research', 'Spectacle computing', 'computational aesthetics', 'generative', 'organic patterns', 'Minimalism', 'traditional Asian art', 'Conceptual art', 'trans-media artwork', 'public art'],
        designTechniques: ['color', 'shape/pattern', 'imagery', 'sound/music', 'animation', 'metaphor/analogy', 'GUI interaction', 'material ', 'style', 'light', 'storyline/plot', 'synchronous/responsive designeffect', 'layout', 'large immersive screen', 'montage/collage', 'perspective of machine', 'co-design/user generated content', 'AR/VR/MR', 'body interaction', 'facial interaction', 'typography', 'situated environment', 'personalization', 'touch/manipulate', 'bionics-triggered interaction', 'gesture interaction', 'plants', 'voice interaction', 'draw/sketch interaction', 'smell', 'robot', 'bacteria', 'temperature', 'taste'],
        // whatPurpose:[ 'experiment', 'inform', 'engage', 'provoke', 'reveal', 'criticize', 'equip', 'analyze', 'advocate', 'socialize', 'witness', 'archive', 'empower', 'commemorate','not specified'],
        // whereField:['environmental sciences & ecology', 'urban studies', 'social issues', 'biology', 'news & media', 'art', 'computer science', 'language & literature', 'health & well-being', 'physics', 'astronomy', 'business', 'daily life', 'psychology', 'politics', 'education', 'math', 'history','not specified'],
        // 添加其他属性的选项数组
        selectedFilters: {
            time: [],
            artPaper: [],
            cluster: [],
            designParadigm: [],
            designTechnique: [],
            whatPurpose: [],
            whereField: [],
            // 添加其他属性的选中状态
        },
        jsonData: [
            {
                // id: 1,
                name: "Item 1",
                time: ["Time 1", "Time 2"],
                artPaper: ["Art/Paper 1"],
                cluster: ["Category 1"],
                designParadigm: ["Paradigm 1"],
                designTechnique: ["Technique 1"],
                whatPurpose: ["Purpose 1"],
                whereField: ["Field 1"],
                // 添加其他属性的数据
            },
            {
                // id: 2,
                name: "Item 2",
                time: ["Time 2"],
                artPaper: ["Art/Paper 2", "Art/Paper 3"],
                cluster: ["Category 2"],
                designParadigm: ["Paradigm 2"],
                designTechnique: ["Technique 2"],
                whatPurpose: ["Purpose 2"],
                whereField: ["Field 2"],
                // 添加其他属性的数据
            },
            // 添加更多数据项
            {
                // id: 2,
                name: "Item 2",
                time: ["Time 2"],
                artPaper: ["Art/Paper 2", "Art/Paper 3"],
                cluster: ["Category 2","Category 1"],
                designParadigm: ["Paradigm 2"],
                designTechnique: ["Technique 2"],
                whatPurpose: ["Purpose 2"],
                whereField: ["Field 2"],
                // 添加其他属性的数据
            },
        ],
        filteredData: [] // 存储筛选后的数据
    } 
   },
             created() {
                 // 初始化时默认全选
                 this.selectAllFilters();
                 // 一开始默认展示所有数据
                //  const jsonFilePath = '../output.json';
                // const jsonFilePath = '/Collection/output.json'
                const jsonFilePath = '/output.json'
                // const jsonFilePath = `${process.env.BASE_URL}output.json`;
                // console.log(jsonFilePath)
                 axios
                   .get(jsonFilePath)
                   .then((response) => {
                     this.jsonData = response.data;
                     this.jsonData.forEach(item => {
                        item.isImageLoaded = false; // 默认值为 false
                    });
                     this.filteredData = this.jsonData;
                     this.loadImages(); 
                   })
                   .catch((error) => {
                     console.error('Error reading local JSON file:', error);
                   });
                   
                   
                 
             },
             mounted() {
                
    this.initializeCanvas();
    this.generateParticles();
    this.draw();
    window.addEventListener('resize', this.handleResize);
  
  },
        methods: {
            tagStyle(technique) {
                return {
                    backgroundColor: this.techniqueColors[technique],
               
            };
                },
                cardStyle(cluster) {
            return {
                borderColor: this.borderColors[cluster],
                borderStyle: "solid",
                borderWidth: "5px",
                borderRadius: "12px"
                // boxShadow: `0 0 10px ${this.borderColors[cluster]}`
            };
        },
                handleMessageFromChild(message) {
    this.selectedFilters.time=message;
    this.applyFilters();
    },
                initializeCanvas() {
      this.canvas = this.$refs.particleCanvas;
      this.ctx = this.canvas.getContext("2d");
      this.canvas.width = window.innerWidth;
      this.canvas.height = window.innerHeight;
    },
    generateParticles() {
      this.numParticles = this.canvas.width / 20;
      for (let i = 0; i < this.numParticles; i++) {
        this.particles.push({
          x: Math.random() * this.canvas.width,
          y: Math.random() * this.canvas.height,
          radius: Math.random() * 10 + 1,
          color:
            "rgb(" +
            Math.floor(Math.random() * 255) +
            "," +
            Math.floor(Math.random() * 255) +
            "," +
            Math.floor(Math.random() * 255) +
            ")",
          speedX: Math.random() * 3 - 1.5,
          speedY: Math.random() * 3 - 1.5
        });
      }
    },
    draw() {
      this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
      this.particles.forEach(particle => {
        this.ctx.beginPath();
        this.ctx.arc(particle.x, particle.y, particle.radius, 0, Math.PI * 2);
        this.ctx.fillStyle = particle.color;
        this.ctx.fill();

        particle.x += particle.speedX;
        particle.y += particle.speedY;

        if (particle.x < 0 || particle.x > this.canvas.width) {
          particle.x = Math.random() * this.canvas.width;
        }
        if (particle.y < 0 || particle.y > this.canvas.height) {
          particle.y = Math.random() * this.canvas.height;
        }
      });
      requestAnimationFrame(this.draw);
    },
    handleResize() {
      this.canvas.width = window.innerWidth;
      this.canvas.height = window.innerHeight;
    },
    beforeDestroy() {
    window.removeEventListener('resize', this.handleResize);
  },

                loadImages() {
                this.jsonData.forEach(item => {
                    const img = new Image();
                    img.onload = () => {
                    item.isImageLoaded = true; // 图片加载完成后设置isImageLoaded为true
                    };
                    // console.log(item)
                    img.src = require('@/assets/images/each/' + item.images);
                });
                },
                isSelected(filterType, index) {
                    // 根据filterType和index检查按钮是否选中
                    return this.selectedFilters[filterType].includes(index);
                },
                 toggleAllFilters() {
                     // 切换全选状态
                     if (this.isAllSelected()) {
                         this.deselectAllFilters();
                     } else {
                         this.selectAllFilters();
                     }
                     this.applyFilters(); // 应用筛选
                 },
                 selectAllFilters() {
                     // 选中所有筛选条件
                     this.selectedFilters.time = [];
                     this.selectedFilters.artPaper = [];
                     this.selectedFilters.cluster = [];
                     this.selectedFilters.designParadigm = [];
                     this.selectedFilters.designTechnique = [];
                     this.selectedFilters.whatPurpose = [];
                     this.selectedFilters.whereField = [];
                     // 添加其他属性的选中状态
                 },
                 deselectAllFilters() {
                     // 取消选中所有筛选条件
                     this.selectedFilters.time = -1;
                     this.selectedFilters.artPaper = -1;
                     this.selectedFilters.cluster = -1;
                     this.selectedFilters.designParadigm = -1;
                     this.selectedFilters.designTechnique = -1;
                     this.selectedFilters.whatPurpose = -1;
                     this.selectedFilters.whereField = -1;
                     // 添加其他属性的选中状态
                 },
                 isAllSelected() {
                     // 检查是否所有筛选条件都已选中
                     return (
                         this.selectedFilters.time.length === 0 &&
                         this.selectedFilters.artPaper.length === 0 &&
                         this.selectedFilters.cluster.length === 0 &&
                         this.selectedFilters.designParadigm.length === 0 &&
                         this.selectedFilters.designTechnique.length === 0 &&
                         this.selectedFilters.whatPurpose.length === 0 &&
                         this.selectedFilters.whereField.length === 0
                         // 检查其他属性的选中状态
                     );
                 },
                 toggleFilter(filterType, index) {
                     // 切换单个筛选条件
                    //  if (this.selectedFilters[filterType] === index) {
                    //      this.selectedFilters[filterType] = null;
                    //  } else {
                    //      this.selectedFilters[filterType] = index;
                    //  }
                    const selectedFilters = this.selectedFilters[filterType];
                    const selectedIndex = selectedFilters.indexOf(index);

                    if (selectedIndex === -1) {
                    // 如果未选中，则添加到已选中列表
                    selectedFilters.push(index);
                    } else {
                    // 如果已选中，则从已选中列表中移除
                    selectedFilters.splice(selectedIndex, 1);
                    }
                     this.applyFilters(); // 应用筛选
                 },
                 applyFilters() {
                     // 执行筛选操作
                     this.filteredData = this.jsonData.filter((item) => {
 
 // if (Array.isArray(item.designCategory)) {
 //   console.log('item.designCategory is an array',item);
 // } else {
 //   console.log('item.designCategory is not an array');
 // }
 
                         const timeFilter =
                             this.selectedFilters.time.length === 0 ||
                            //  item.time.some((time) => time === this.times[this.selectedFilters.time]);
                             this.selectedFilters.time.some((selectedIndex) => item.time.includes(this.times[selectedIndex]));
                         const artPaperFilter =
                             this.selectedFilters.artPaper.length === 0 ||
                             this.selectedFilters.artPaper.some((selectedIndex) => item.artPaper.includes(this.artPapers[selectedIndex]));
                         const clusterFilter =
                             this.selectedFilters.cluster.length === 0 ||
                             this.selectedFilters.cluster.some((selectedIndex) => item.cluster.includes(this.designCategories[selectedIndex]));
                         const designParadigmFilter =
                             this.selectedFilters.designParadigm.length === 0 ||
                             this.selectedFilters.designParadigm.some((selectedIndex) => item.designParadigm.includes(this.designParadigms[selectedIndex]));
                         const designTechniqueFilter =
                         this.selectedFilters.designTechnique.length === 0 ||
                             this.selectedFilters.designTechnique.some((selectedIndex) => item.designTechnique.includes(this.designTechniques[selectedIndex]));
                         const whatPurposeFilter =
                             this.selectedFilters.whatPurpose.length === 0 ||
                             this.selectedFilters.whatPurpose.some((selectedIndex) => item.whatPurpose.includes(this.whatPurpose[selectedIndex]));
                         const whereFieldFilter =
                             this.selectedFilters.whereField.length === 0 ||
                             this.selectedFilters.whereField.some((selectedIndex) => item.whereField.includes(this.whereField[selectedIndex]));
                         // 添加其他属性的筛选逻辑
                         return (
                             timeFilter &&
                             artPaperFilter &&
                             clusterFilter &&
                             designParadigmFilter &&
                             designTechniqueFilter &&
                             whatPurposeFilter &&
                             whereFieldFilter
                             // 添加其他属性的筛选逻辑
                         );
                     });
                 }
               }
   
 }
 </script>
 
